import Vue from 'vue'
import VueRouter from 'vue-router'

// 路由懒加载  按组分块
// import Login from '../components/Login.vue'
const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Login.vue')
// import Home from '../components/Home'
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home')
// import Welcome from '../components/Welcome'
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome')

// import Users from '../components/user/Users'
const Users = () => import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/user/Users')
// import Rights from '../components/power/Rights'
const Rights = () => import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/power/Rights')
// import Roles from '../components/power/Roles'
const Roles = () => import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/power/Roles')

// import Cate from '../components/goods/Cate'
const Cate = () => import(/* webpackChunkName: "Cate_Params" */ '../components/goods/Cate')
// import Params from '../components/goods/Params'
const Params = () => import(/* webpackChunkName: "Cate_Params" */ '../components/goods/Params')

// import GoodsList from '../components/goods/GoodsList'
const GoodsList = () => import(/* webpackChunkName: "GoodsList" */ '../components/goods/GoodsList')
// import GoodsAdd from '../components/goods/GoodsAdd'
const GoodsAdd = () => import(/* webpackChunkName: "GoodsList" */ '../components/goods/GoodsAdd')
// import GoodsUpdate from '../components/goods/GoodsUpdate'
const GoodsUpdate = () => import(/* webpackChunkName: "GoodsList" */ '../components/goods/GoodsUpdate')

// import Order from '../components/order/Order'
const Order = () => import(/* webpackChunkName: "Order_Report" */ '../components/order/Order')
// import Report from '../components/report/Report'
const Report = () => import(/* webpackChunkName: "Order_Report" */ '../components/report/Report')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      {
        path: '/welcome',
        component: Welcome
      },
      {
        path: '/users',
        component: Users
      },
      {
        path: '/rights',
        component: Rights
      },
      {
        path: '/roles',
        component: Roles
      },
      {
        path: '/categories',
        component: Cate
      },
      {
        path: '/params',
        component: Params
      },
      {
        path: '/goods',
        component: GoodsList
      },
      {
        path: '/goods/add',
        component: GoodsAdd
      },
      {
        path: '/goods/update',
        component: GoodsUpdate
      },
      {
        path: '/orders',
        component: Order
      },
      {
        path: '/reports',
        component: Report
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
  // to将要访问的路径
  // from 代表从哪个路径跳转而来
  // next 放行函数
  // 如果用户要访问的是login页面则直接放行
  // 存储home导航栏高亮指引状态
  window.sessionStorage.setItem('activePath', to.path)
  if (to.path === '/login') return next()
  // 获取sessionStorage中的token值
  const tokenStr = window.sessionStorage.getItem('token')
  // 如果没有获取到token值，则一直重定向为登录页面
  if (!tokenStr) return next('/login')
  // 若获取到token值则放行
  next()
})

export default router
