import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 验证邮箱地址正则
var checkEmail = (rule, value, callback) => {
  const regEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  if (regEmail.test(value)) {
    return callback()
  }
  callback(new Error('请输入合法的邮箱地址'))
}

// 验证手机号码正则
var checkMobile = (rule, value, callback) => {
  const regMobile = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/
  if (regMobile.test(value)) {
    return callback()
  }
  callback(new Error('请输入合法的手机号码'))
}

const store = new Vuex.Store({
  state: {
    username: [{
      required: true,
      message: '请输入用户名',
      trigger: 'blur'
    },
    {
      min: 3,
      max: 10,
      message: '长度在3到10个字符之间',
      trigger: 'blur'
    }
    ],
    password: [{
      required: true,
      message: '请输入密码',
      trigger: 'blur'
    },
    {
      min: 5,
      max: 15,
      message: '长度在5到15个字符之间',
      trigger: 'blur'
    }
    ],
    email: [{
      required: true,
      message: '请输入邮箱',
      trigger: 'blur'
    },
    {
      validator: checkEmail,
      trigger: 'blur'
    }
    ],
    mobile: [{
      required: true,
      message: '请输入手机号码',
      trigger: 'blur'
    },
    {
      validator: checkMobile,
      trigger: 'blur'
    }
    ],
    // 角色列表数据
    rolesList: [],
    // 动态参数数据
    manyList: [],
    // 静态属性数据
    onlyList: [],
    // 级联选择器选择后所返回的数组(由分类各级ID组成)
    selectedArray: []
  },
  mutations: {
    // 获取角色列表数据
    async getRolesList(state) {
      const {
        data: res
      } = await Vue.prototype.$http.get('roles')
      if (res.meta.status !== 200) {
        return Vue.prototype.$message.error('获取角色列表失败')
      }
      state.rolesList = res.data
    },
    // 获取分类参数
    async getCateParamsVuex(state, {
      paramTypes,
      isString
    }) {
      // 第二个参数 isString为可选参数 若为true 则保留数据中attr_vals为原字符串格式
      const {
        data: res
      } = await Vue.prototype.$http.get(
        `categories/${store.getters.cateId}/attributes`, {
          params: {
            sel: paramTypes
          }
        }
      )
      if (res.meta.status !== 200) {
        return Vue.prototype.$message.error('获取参数失败')
      }
      if (isString !== true) {
        res.data.forEach(item => {
          item.attr_vals.trim() !== ''
            ? (item.attr_vals = item.attr_vals.split(' '))
            : (item.attr_vals = [])
        })
      }
      if (paramTypes === 'many') {
        state.manyList = res.data
      } else {
        state.onlyList = res.data
      }
    },
    // 级联选择器变化事件
    handleChangeVuex(state, {
      selectedKeys,
      stringFormat
    }) {
      state.selectedArray = selectedKeys
      // 设置为只能选择第三级分类
      if (state.selectedArray.length !== 3) {
        state.selectedArray = []
      } else {
        // 获取分类动态参数
        store.commit('getCateParamsVuex', { paramTypes: 'many' })
        // 获取分类静态属性
        store.commit('getCateParamsVuex', { paramTypes: 'only', isString: stringFormat })
      }
      // 若选择的是非三级分类则清空表格数据
      if (state.selectedArray.length === 0) {
        state.manyList = []
        state.onlyList = []
      }
    }
  },
  getters: {
    // 获取所选中三级分类ID
    cateId(state) {
      if (state.selectedArray.length === 3) {
        return state.selectedArray[2]
      } else {
        return null
      }
    },
    // 添加参数按钮禁用控制
    isBtnDisable(state) {
      if (state.selectedArray.length === 3) {
        return false
      } else {
        return true
      }
    }
  }
})

export default store
